<template>
    <div class="swap-auto flex-row-start">
        <div class="auto MR-20">
            <el-tabs v-model="activeName" class="swap-tabs auto-tabs">
                <el-tab-pane label="控制面板" name="address">
                    <div class="auto-tab__list">
                        <div class="auto-list__item flex-row-start align-center" v-for="(item, index) in logList" :key="index">
                            <el-icon class="MR-5"><Clock /></el-icon>
                            <div class="MR-20">{{moment(item.date).format('HH:mm:ss')}}</div>
                            <el-icon class="MR-10"><Warning :color="item.type == -1 ? 'red' : 'green'"/></el-icon>
                            <div class="flex-1 text-overflow__1">{{item.message}}</div>
                        </div>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="导入账号" name="account">
                    <el-input type="textarea" :disabled="isOpen" v-model="accounts" resize="none" class="auto-tab__account"></el-input>
                </el-tab-pane>
            </el-tabs>
            <div class="flex-row-between MT-20">
                <div class="text-24 flex-row-start align-center">抢链模式（股权≥50）， 什么是股权<el-icon class="ML-5"><QuestionFilled /></el-icon></div>
                <el-switch v-model="hignTest" :disabled="isOpen" size="large"/>
            </div>
            <div class="buy-tips MT-15">在开启任务的时候会无视池子是否添加总是会尝试购买</div>
            <div class="MT-30">
                <div class="flex-row-between MT-20">
                    <div class="text-24 flex-row-start align-center">极速模式（股权≥50）， 什么是股权<el-icon class="ML-5"><QuestionFilled /></el-icon></div>
                    <el-switch v-model="speedMode" :disabled="isOpen" size="large"/>
                </div>
                <div class="buy-input flex-row-end text-16 MT-20">
                    <input type="text" :disabled="isOpen" v-model="hignRate" class="buy-input__info text-right" placeholder="0">
                </div>
            </div>
            <div class="buy-tips MT-15">极速模式是会提前设置好Gas上限</div>
            <div class="buy-tips red MT-15">非极速模式下是会先去检测需要多少Gas上限再执行购买，理论上来说会慢一拍！</div>
            <div class="flex-row-between MT-20">
                <div class="text-24">貔貅检测</div>
                <el-switch :disabled="isOpen" v-model="braveTroopsTest" size="large"/>
            </div>
            <div class="buy-tips MT-15">如果不确定币方项目方是否靠谱可以开启本选项。开启后买入成功会 自动检测能否卖出，如果不能卖出会回滚交易。</div>
            <div class="buy-tips red MT-15">如果确定币方项目方靠谱，无需开启。 特别注意:该功能只能检测特定方式的貔貅，并不保证百分百识别。 因为貔貅的套路太多，功能会</div>
            <div class="flex-row-between MT-30">
                <div class="text-24">延迟购买（秒）</div>
                <div class="buy-input flex-row-end text-16">
                    <input type="text" :disabled="isOpen" class="buy-input__info text-right" placeholder="0">
                </div>
            </div>
            <div class="buy-tips MT-15">指定延迟时间购买，0为不延迟。比如输入1，则必定在延迟l秒后买入。</div>
            <div class="buy-tips MT-15">BSC链l个区块3秒，如果你期望延迟区块以后买入，就输入3。(该功能只在普通模式下生效)</div>
        </div>
        <div class="swap-info PB-20">
            <div class="swap-item">
            <div class="swap-item__top flex-row-between">
                <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item1')">
                    <img v-lazy="getLogo(item1.address)" class="swap-item__top-icon MR-10"/>
                    <div class="swap-item__top-name text-overflow__1">{{item1.symbol}}</div>
                    <el-icon class="ML-5">
                    <ArrowDown />
                    </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item1.address)"><CopyDocument /></el-icon>
                </div>
                <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item1.loading">
                    <Loading />
                </el-icon>
                <template v-else-if="item1.balance">
                    {{ t('swap.balance') }}：{{getBit(utils.formatUnits(item1.balance, item1.decimals), 5) }}
                </template>
                </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
                <input :disabled="!isLogin || isOpen" class="text-right flex-1" v-bit v-model="item1.price" @input="price1Change('item1')" placeholder="0.00">
                <el-tag class="ML-10 ml-2 pointer" effect="light" v-if="isLogin" @click="maxHandler" type="warning" size="small">MAX</el-tag>
            </div>
            </div>
            <div class="swap-item">
            <div class="swap-item__top flex-row-between">
                <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item2')">
                    <img v-lazy="getLogo(item2.address)" class="swap-item__top-icon MR-10"/>
                    <div class="swap-item__top-name text-overflow__1">{{item2.symbol}}</div>
                    <el-icon class="ML-5">
                    <ArrowDown />
                    </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item2.address)"><CopyDocument /></el-icon>
                </div>
                <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item2.loading">
                    <Loading />
                </el-icon>
                <template v-else-if="item2.balance">
                    {{ t('swap.balance') }}：{{getBit(utils.formatUnits(item2.balance, item2.decimals), 5)}}
                </template>
                </div>
            </div>
            </div>
            <div class="swap-slippage PL-20 PR-20 MT-10">
            
            <div class="swap-slippage__select flex-row-between pointer">
                <div class="text-16">GWEI</div>
                <div class="flex-row-end">
                <el-tag 
                    class="swap-slippage__select-tag" 
                    type="warning" 
                    :effect="gWei == item ? 'dark' : 'light'" 
                    @click="toggleGwei(item)"
                    v-for="item in gWeiList"
                    :key="item">
                    {{ item }}
                </el-tag>
                <input type="number" class="swap-slippage__select-input" :disabled="isOpen" placeholder="5" v-model.number="gWei">
                </div>
            </div>
            </div>
            <div class="PL-20 PR-20 MT-30">
            <div class="flex-row-between MB-10">
                <div class="text-yellow">{{ t('swap.tolerance') }}</div>
                <div class="text-blue">{{progress}}%</div>
            </div>
            <div>
                <el-slider v-model="progress" :disabled="isOpen" :min="1" :max="99"/>
                <div class="flex-row-between text-yellow">
                <div>0%</div>
                <div>100%</div>
                </div>
            </div>
            <div class="MT-30 text-28">
                买入
            </div>
            <div class="flex-row-start flex-wrap">
                <div class="flex-row-start buy-item align-center">
                <span>购买次数</span>
                <input type="text" :disabled="isOpen" v-model="buyNum" placeholder="0.00" class="buy-item__input flex-1">
                </div>
                <div class="flex-row-start buy-item align-center">
                <span>流通小于</span>
                <input type="text" :disabled="isOpen" placeholder="0.00" class="buy-item__input flex-1" v-model="circulationLt">
                </div>
                <div class="flex-row-start buy-item align-center">
                <span>流通大于</span>
                <input type="text" :disabled="isOpen" placeholder="0.00" class="buy-item__input flex-1" v-model="circulationGt">
                </div>
            </div>
            </div>
            <el-button v-if="!isLogin" class="swap-btn MT-30" @click="loginFn">{{ t('swap.connectWallet') }}</el-button>
            <template v-else>
                <el-button v-if="!item1.price" disabled class="swap-btn MT-30"> {{ t('swap.enterAmount') }} </el-button>
                <el-button v-else class="swap-btn MT-30" @click="startHandler">
                    <div class="flex-row-center">
                        <el-icon class="is-loading MR-5" v-if="isOpen">
                            <Loading />
                        </el-icon>
                        <span>{{ isOpen ? '停止' : '开始'}}</span>
                    </div>
                </el-button>
            </template>
        </div>
    </div>

    <Tokens ref="tokens" @change="tokenChange" @update="tokenUpdate"/>
</template>

<script>
import { product } from '@/plugins/wallet/config.json'
import ABI from '@/api/abis/SwapHelper.json'
import PAIR_ABI from '@/api/abis/getPair.js'
import { getBit, Tips, getLogo } from '../../libs/utils'
import Tokens from './modal/tokens.vue'
import { utils, BigNumber } from 'ethers'
import { toRaw } from 'vue'
import { mapGetters } from 'vuex'
import { ElNotification } from 'element-plus'
import moment from 'moment'
const abi = [{"inputs":[],"payable":false,"stateMutability":"nonpayable","type":"constructor"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"owner","type":"address"},{"indexed":true,"internalType":"address","name":"spender","type":"address"},{"indexed":false,"internalType":"uint256","name":"value","type":"uint256"}],"name":"Approval","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"sender","type":"address"},{"indexed":false,"internalType":"uint256","name":"amount0","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"amount1","type":"uint256"},{"indexed":true,"internalType":"address","name":"to","type":"address"}],"name":"Burn","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"sender","type":"address"},{"indexed":false,"internalType":"uint256","name":"amount0","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"amount1","type":"uint256"}],"name":"Mint","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"sender","type":"address"},{"indexed":false,"internalType":"uint256","name":"amount0In","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"amount1In","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"amount0Out","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"amount1Out","type":"uint256"},{"indexed":true,"internalType":"address","name":"to","type":"address"}],"name":"Swap","type":"event"},{"anonymous":false,"inputs":[{"indexed":false,"internalType":"uint112","name":"reserve0","type":"uint112"},{"indexed":false,"internalType":"uint112","name":"reserve1","type":"uint112"}],"name":"Sync","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"from","type":"address"},{"indexed":true,"internalType":"address","name":"to","type":"address"},{"indexed":false,"internalType":"uint256","name":"value","type":"uint256"}],"name":"Transfer","type":"event"},{"constant":true,"inputs":[],"name":"DOMAIN_SEPARATOR","outputs":[{"internalType":"bytes32","name":"","type":"bytes32"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"MINIMUM_LIQUIDITY","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"PERMIT_TYPEHASH","outputs":[{"internalType":"bytes32","name":"","type":"bytes32"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"internalType":"address","name":"","type":"address"},{"internalType":"address","name":"","type":"address"}],"name":"allowance","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"spender","type":"address"},{"internalType":"uint256","name":"value","type":"uint256"}],"name":"approve","outputs":[{"internalType":"bool","name":"","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[{"internalType":"address","name":"","type":"address"}],"name":"balanceOf","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"to","type":"address"}],"name":"burn","outputs":[{"internalType":"uint256","name":"amount0","type":"uint256"},{"internalType":"uint256","name":"amount1","type":"uint256"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"decimals","outputs":[{"internalType":"uint8","name":"","type":"uint8"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"factory","outputs":[{"internalType":"address","name":"","type":"address"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"getReserves","outputs":[{"internalType":"uint112","name":"_reserve0","type":"uint112"},{"internalType":"uint112","name":"_reserve1","type":"uint112"},{"internalType":"uint32","name":"_blockTimestampLast","type":"uint32"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"_token0","type":"address"},{"internalType":"address","name":"_token1","type":"address"}],"name":"initialize","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"kLast","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"to","type":"address"}],"name":"mint","outputs":[{"internalType":"uint256","name":"liquidity","type":"uint256"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"name","outputs":[{"internalType":"string","name":"","type":"string"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[{"internalType":"address","name":"","type":"address"}],"name":"nonces","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"owner","type":"address"},{"internalType":"address","name":"spender","type":"address"},{"internalType":"uint256","name":"value","type":"uint256"},{"internalType":"uint256","name":"deadline","type":"uint256"},{"internalType":"uint8","name":"v","type":"uint8"},{"internalType":"bytes32","name":"r","type":"bytes32"},{"internalType":"bytes32","name":"s","type":"bytes32"}],"name":"permit","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"price0CumulativeLast","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"price1CumulativeLast","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"to","type":"address"}],"name":"skim","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":false,"inputs":[{"internalType":"uint256","name":"amount0Out","type":"uint256"},{"internalType":"uint256","name":"amount1Out","type":"uint256"},{"internalType":"address","name":"to","type":"address"},{"internalType":"bytes","name":"data","type":"bytes"}],"name":"swap","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"symbol","outputs":[{"internalType":"string","name":"","type":"string"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[],"name":"sync","outputs":[],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":true,"inputs":[],"name":"token0","outputs":[{"internalType":"address","name":"","type":"address"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"token1","outputs":[{"internalType":"address","name":"","type":"address"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":true,"inputs":[],"name":"totalSupply","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"payable":false,"stateMutability":"view","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"to","type":"address"},{"internalType":"uint256","name":"value","type":"uint256"}],"name":"transfer","outputs":[{"internalType":"bool","name":"","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"},{"constant":false,"inputs":[{"internalType":"address","name":"from","type":"address"},{"internalType":"address","name":"to","type":"address"},{"internalType":"uint256","name":"value","type":"uint256"}],"name":"transferFrom","outputs":[{"internalType":"bool","name":"","type":"bool"}],"payable":false,"stateMutability":"nonpayable","type":"function"}]
let contract;
let addressPath = []
let path = [];
export default {
    data() {
        return {
            getLogo,
            getBit,
            moment,
            utils,
            activeName: 'address',
            gWei: 5,
            gWeiList: [5, 7, 10, 15, 20],
            progress: 10,
            balanceTimer: null,
            buyLoading: false,
            // 第一个币种信息
            item1: {
                name: "BNB",
                symbol: "BNB",
                address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
                balance: '',
                price: '',
                loading: false
            },
            // 第二个币种信息
            item2: {
                name: "BUSD",
                symbol: "BUSD",
                address: "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56",
                balance: '',
                price: '',
                loading: false
            },
            accounts: '',
            // 是否登录
            isLogin: false,
            // 购买次数
            buyNum: 1,
            // 当前流通量
            circulation: '',
            // 流通小于多少
            circulationLt: '',
            // 流通大于多少
            circulationGt: '',
            // 日志记录
            logList: [],
            // 是否开启高燃烧检测
            hignTest: false,
            // 是否开启极速模式
            speedMode: false,
            // 燃烧上限比例
            hignRate: 2000000,
            // 是否开启貔貅检测
            braveTroopsTest: false,
            // 延迟购买时间
            delayTime: '',
            // 是否开启
            isOpen: false,
        }
    },
    components: {
        Tokens
    },
    directives: {
        bit: {
            updated(el) {
                el.value = el.value ? getBit(el.value, 5) : ''
            }
        }
    },
    computed: {
        ...mapGetters(['currentNetwork', 'networkList', 'currentDex']),
    },
    mounted() {
        if (window.wallet.accounts[0] && window.wallet.connectState == 3) {
            this.isLogin = true    
        }
        this.isLogin && this.loginCallBack()
        window.wallet.on('accountChange', () => {
            if (window.wallet.accounts[0] && window.wallet.connectState == 3) {
                this.isLogin = true    
            }
            this.isLogin && this.loginCallBack()
        })
    },
    methods: {
        // 更新币种信息
        tokenUpdate({address, name, symbol}) {
            if (this.item1.address == address) {
                this.item1.name = name
                this.item1.symbol = symbol
            } else if (this.item2.address == address) {
                this.item2.name = name
                this.item2.symbol = symbol
            }
        },
        // 修改需要转换的币
        async tokenChange(data) {
            if (this.type == 'item1') {
                if (this.item2.address == data.address) {
                    this.item2 = toRaw(this.item1)
                }
            } else {
                if (this.item1.address == data.address) {
                    if (this.item1.address == data.address) {
                        this.item1 = toRaw(this.item2)
                    }
                }
            }
            data.price = data.price || '0'
            this[this.type] = data
            this.isLogin && this.loginCallBack()
            // this.price1Change(this.type == 'item1' ? 'item2' : 'item1')
        },
        toggleGwei(data) {
            if (this.isOpen) {
                this.gWei = data
            }
        },
        startHandler() {
            if (!this.circulationLt && !this.circulationGt) {
                ElNotification({
                    type: 'error',
                    title: '温馨提示',
                    message: '请输入流通范围'
                })
                return
            }
            if (this.speedMode && !this.hignRate) {
                ElNotification({
                    type: 'error',
                    title: '温馨提示',
                    message: '极速模式下请输入Gas上限'
                })
                return
            }
            this.isOpen = !this.isOpen
            // 抢链模式，直接购买
            if (this.hignTest) {
                this.confirmBuy()
            } else {
                setTimeout(() => {
                    this.refreshCirculation()
                }, this.delayTime)
            }
            
        },
        // 登陆
        loginFn() {
            let key = prompt('输入私钥')
            if (!key) {
                return
            }
            window.wallet.connect(3, key)
        },
        // 价格变化
        async price1Change(key) {
            let otherKey = key == 'item1' ? 'item2' : 'item1'
            this[key].price = getBit(this[key].price.replace(/[^0-9.]/g,''), 5) 
            if (this[key].price == '') {
                this[key].price = ''
                return
            }

            clearInterval(this.timer)
            this.timer = setInterval(() => {
                this.price1Change(key)
            }, 3000)
            
            const price = this[key].price.toString()
            if (price === '0.') {
                return
            }
            let funKey = key == 'item1' ? 'getAmountsOut' : 'getAmountsIn'
            let promises = [window.wallet.ContractWETH.getAmountsOut(utils.parseUnits(price, this[key].decimals), [
                this.item1.address,
                this.item2.address
            ])]
            addressPath = [[this.item1.address,this.item2.address]]
            this.currentNetwork.TransferTokens.forEach(item => {
                if (item.address != this[key].address && item.address != this[otherKey].address) {
                addressPath.push([this.item1.address,item.address,this.item2.address])
                promises.push(window.wallet.ContractWETH[funKey](utils.parseUnits(price, this[key].decimals), [
                    this.item1.address,
                    item.address,
                    this.item2.address
                ]))
                }
            })
            Promise.allSettled(promises).then(data => {
                let result = '';
                let lastIndex;
                data.forEach((item, index) => {
                    const {status, value} = item
                    if (status == "fulfilled") {
                    const last = key == 'item1' ? value[value.length - 1] : value[0]
                    if (!result) {
                        result = last
                        lastIndex = index
                    } else if (last.gt(result)) {
                        result = last
                        lastIndex = index
                    }
                    }
                })
                if (result && this[key].price && this[key].price != '0') {
                    console.log(lastIndex, '最多可以换'+utils.formatUnits(result.toString(), this[otherKey].decimals)+'-----------'+result.toString())
                    this[otherKey].price = utils.formatUnits(result, this[otherKey].decimals)
                    path = addressPath[lastIndex]
                }
            })
        },
        async loginCallBack(loading = true) {
            clearTimeout(this.balanceTimer)
            this.item1.loading = loading
            this.item2.loading = loading
            this.getItemBalance(this.item1.address, 'item1').then(({balance, decimals}) => {
                this.item1.balance = balance
                this.item1.decimals = decimals
                this.item1.loading = false
            })
            this.getItemBalance(this.item2.address, 'item2').then(({balance, decimals}) => {
                this.item2.balance = balance
                this.item2.decimals = decimals
                this.item2.loading = false
            })
            this.balanceTimer = setTimeout(() => {
                this.loginCallBack(false)
            }, 3000)
            if (!contract) {
                contract = window.wallet.initContract(product.SWAP_HELPER, ABI)
                window._contract = contract
            }
        },
        // 满足条件提交购买
        async confirmBuy() {
            if (this.buyLoading || !this.isOpen) {
                return
            }
            this.logList.unshift({
                type: 0,
                date: new Date(),
                message: '满足条件，开始购买'
            })
            let accounts = window.wallet.accounts
            if (this.accounts) {
                const arr = this.accounts.split('\n')
                accounts = arr
            }
            const price = utils.parseUnits(this.item1.price.toString(), 18)
            const value = this.item1.price * this.buyNum * accounts.length
            
            const fee = await contract.callFee()
            let valueBigNumber = utils.parseUnits(value.toString(), 18)
            valueBigNumber = valueBigNumber.add(fee)
            // 貔貅检测费用
            if (this.braveTroopsTest) {
                const TxPrice = await contract.checkTxPrice()
                valueBigNumber = valueBigNumber.add(TxPrice)
            }
            console.log('price', this.item1.price, price)
            console.log('buyNum', this.buyNum)
            console.log('progress', this.progress)
            console.log('braveTroopsTest', this.braveTroopsTest)
            console.log('accounts', accounts)
            console.log('path', path)
            console.log('value', value, valueBigNumber)
            this.buyLoading = true
            try {
                let gasLimit 
                if (this.speedMode) {
                    gasLimit = utils.parseUnits(this.hignRate.toString(), 18)
                } else {
                    gasLimit = await contract.estimateGas.manyTokensBuy(
                        price, 
                        this.buyNum, 
                        this.progress, 
                        this.braveTroopsTest, 
                        accounts, 
                        path, 
                        {
                            value: valueBigNumber,
                            gasPrice: utils.parseUnits(this.gWei.toString(), 18),
                        }
                    )
                }
                

                const tx = await contract.manyTokensBuy(
                    price, 
                    this.buyNum, 
                    this.progress, 
                    this.braveTroopsTest, 
                    accounts, 
                    path, 
                    {
                        value: valueBigNumber,
                        gasPrice: utils.parseUnits(this.gWei.toString(), 18),
                        gasLimit: gasLimit.mul(135).div(100)
                    }
                )
                await tx.wait()
                this.logList.unshift({
                    type: 1,
                    date: new Date(),
                    message: `Swap ${(this.item1.price)} ${this.item1.symbol} for ${(this.item2.price)} ${this.item2.symbol}`
                })
                this.isOpen = false
                this.buyLoading = false
            } catch (error) {
                this.logList.unshift({
                    type: -1,
                    date: new Date(),
                    message: error.reason
                })
                this.buyLoading = false
            }
            this.refreshCirculation()
        },

        // 获取币种余额
        async getItemBalance(address, keyType) {
            let balance;
            const contract = window.wallet.initContract(address)
            const decimals = await contract.decimals()
            if (address == this.currentNetwork.MainCoin.address) {
                balance = await window.wallet.provider.getBalance()
            } else {
                if (keyType == 'item1') {
                    try {
                        const data = await contract.allowance(window.wallet.accounts[0], this.currentDex.address)
                        const res = Math.floor(Number(utils.formatUnits(data, decimals)))
                        if (res <= 0) {
                            this.hasApprove = false
                            await this.approveHandler()
                            this.hasApprove = true
                        }
                    } catch (error) {
                        Tips(error)
                    }
                }
                balance = await contract.balanceOf(window.wallet.accounts[0])
            }
            return {
                balance,
                decimals
            }
        },
        // 授权
        async approveHandler() {
            try {
                const contract = window.wallet.initContract(this.item1.address)
                const tx = await contract.approve(
                    this.currentDex.address,
                    utils.parseEther('10000000000000'),
                    {
                        gasPrice: utils.parseUnits('10', 9)
                    }
                )
                await tx.wait();
                this.hasApprove = true
            } catch (error) {
                Tips(error)
            }
        },
        maxHandler() {
            if (this.isOpen) {
                return
            }
            let price = toRaw(this.item1.balance)
            this.item1.price = utils.formatUnits(price, this.item1.decimals)
        },
        openTokensDialog(type) {
            if (this.isOpen) {
                return
            }
            this.type = type
            this.$refs.tokens.open([this.item1.address, this.item2.address])
        },
        copyHandler(address) {
            this.$emit('copy', address)
        },
        // 刷新流通量
        async refreshCirculation() {
            // 没开启，购买中，抢链模式不刷新流通量
            if (!this.isOpen || this.buyLoading || this.hignTest) {
                return
            }
            
            this.pairContract = window.wallet.initContract('0xcA143Ce32Fe78f1f7019d7d551a6402fC5350c73', PAIR_ABI)
            const data = await this.pairContract.getPair(
                this.item1.address, 
                this.item2.address
            )
            const contract = window.wallet.initContract(data, abi)
            console.log(contract)
            const token0 = await contract.token0()
            const token1 = await contract.token1()
            const res = await contract.getReserves()
            const decimals = await contract.decimals()
            let token;
            if (token0.toLocaleLowerCase() === this.item2.address.toLocaleLowerCase()) {
                this.circulation = res._reserve1
                token = token1
            } else if (token1.toLocaleLowerCase() === this.item2.address.toLocaleLowerCase()) {
                this.circulation = res._reserve0
                token = token0
            }
            const circulationContract = window.wallet.initContract(token)
            const symbol = await circulationContract.symbol()


            const circulation = utils.formatUnits(this.circulation.toString(), decimals)
            this.logList.unshift({
                type: 0,
                date: new Date(),
                message: `检测流动性: ${getBit(circulation)} ${symbol}`
            })
            // 流通量小于多少
            if (this.circulationLt) {
                let circulationLt = utils.parseUnits((this.circulationLt).toString(), decimals)
                if (!this.circulation.lte(circulationLt)) {
                    this.logList.unshift({
                        type: -1,
                        date: new Date(),
                        message: '资金池流通量不满足需求，稍后重试'
                    })
                    return
                }
            }
            // 流通量大于多少
            if (this.circulationGt) {
                let circulationGt = utils.parseUnits((this.circulationGt).toString(), decimals)
                if (!this.circulation.gte(circulationGt)) {
                    this.logList.unshift({
                        type: -1,
                        date: new Date(),
                        message: '资金池流通量不满足需求，稍后重试'
                    })
                    return
                }
            }
            this.confirmBuy()
            this.refreshCirculation()
            console.log(this.circulation, '满足条件')
        }
    }
}
</script>

